<template>
  <div class="transfer-contanier clearfix">
    <input type="hidden"  ref="input" :value="value" @input="updateValue($event.target.value)">
    <div class="inner clearfix">
      <div class="tree-left">
          <div class="button-box">
              <el-input
                size="mini"
                clearable
                placeholder="输入关键字进行过滤"
                v-model="filterText">
              </el-input>
              <el-button icon="el-icon-search" size="mini" style="margin-left:5px;">查询</el-button>
          </div>
          <div class="list-member">
            <h2>常选人员</h2>
            <ul>
              <li v-for="(item,index) in defaultMember" :key="item.id" @click="memberSelect(index,item.id,item.name)" :class="{'li-focus' : chooseNum==index}" ref="memberLi">
                <i class="el-icon-check"></i>
                {{item.name}}
              </li>
            </ul>
          </div>
          <div class="tree-box">
            <el-tree
              class="filter-tree"
              empty-text='暂无数据'
              show-checkbox
              default-expand-all
              :expand-on-click-node="false"
              :props="defaultProps"
              :default-checked-keys="myValue"
              :data="treeData"
              :filter-node-method="filterNode"
              node-key="id"
              @check-change='checkChange'
              ref="tree">
            </el-tree>
          </div>

      </div>
      <div class="list-right">
        <div class="select">
            <el-select
              style="width:100%;"
              v-model="valueSelect"
              filterable
              clearable
              size="mini"
              @change="handleChange"
              placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.groupId"
                :label="item.groupName"
                :value="item.groupId">
                <span style="float: left">{{ item.groupName }}</span>
                <span style="float: right; color: #8492a6; font-size:12px;display: none;" class="icon-nav">
                  <i @click.stop="defaultSetting(item.groupId)">默认</i><i style="margin-left:10px;" @click.stop="deleteGroupMember(item.groupId)">删除</i>
                </span>
              </el-option>
            </el-select>
        </div>
        <div class="choose-member-content">
            <div class="tag-box">
              <el-tag
                style="margin-right:10px;"
                v-for="tag in chooseMember"
                :key="tag.id"
                closable
                size='mini'
                @close="tagClose(tag.id)"
                type="success">
                {{tag.label}}
              </el-tag>
            </div>

        </div>
        <div class="button-nav">
            <el-button style="background-color: #b50005;" type="primary" size="mini" icon="el-icon-delete" @click="all_delete">全部删除</el-button>
            <el-button style="background-color: #b50005;" type="primary" size="mini" icon="el-icon-edit" @click="dialogTableVisible = true">保存分组</el-button>
        </div>
      </div>
    </div>
    <el-dialog title="自定义分组" :visible.sync="dialogTableVisible">
      <div class="edit-box">
          <el-input
            placeholder="请输入内容"
            v-model="editInput"
            clearable>
          </el-input>
          <p>
            保存为默认分组
          </p>
          <el-button  type="primary" size="mini" icon="el-icon-success" style="margin-left:10px;background-color: #b50005;" @click="save_group_name">确认</el-button>

      </div>
    </el-dialog>

    <div class="button-bottom">
      <button @click="handleSave"><img :src="img" alt=""> 保存</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: {
    value: [String, Number],
    contextPath: {
      //请求地址
      type: String,
      default: "/yhhr-web"
    }
  },
  data() {
    return {
      editInput: "",
      dialogTableVisible: false,
      img: require("@/assets/images/save.png"),
      chooseNum: null,
      filterText: "",
      treeData: [
        {
          id: 10,
          label: "人力资源部",
          type:"ORG",
          children: [
            {
              id: 4,
              label: "周霞",
              type:"PENSON"
            }
          ]
        },
        {
          id: 20,
          type:"ORG",
          label: "元禾华创",
          children: [
            {
              id: 5,
              label: "合伙人",
              type:"ORG",
              children: [
                {
                  id: 1,
                  label: "吴海滨",
                  type:"PENSON"
                }
              ]
            }
          ]
        },
        {
          id: 30,
          label: "战略发展部",
          type:"ORG",
          children: [
            {
              id: 2,
              label: "陈高洁",
              type:"PENSON"
            },
            {
              id: 6,
              label: "金祖庆",
              type:"PENSON"
            }
          ]
        },
        {
          id: 40,
          label: "战略发展部1",
          type:"ORG",
          children: [
            {
              id: 21,
              label: "陈高洁2",
              type:"PENSON"
            },
            {
              id: 61,
              label: "金祖庆3",
              type:"PENSON"
            }
          ]
        },
        {
          id: 50,
          label: "战略发展部2",
          type:"ORG",
          children: [
            {
              id: 211,
              label: "陈高洁3",
              type:"PENSON"
            },
            {
              id: 611,
              label: "金祖庆4",
              type:"PENSON"
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      },
      defaultMember: [
        {
          name: "吴海滨",
          id: 1
        },
        {
          name: "陈高洁",
          id: 2
        },
        {
          name: "方亮",
          id: 3
        },
        {
          name: "周霞",
          id: 4
        },
        {
          name: "金祖庆",
          id: 6
        }
      ], //常选人员
      chooseMember: [],
      options: [],//select-options
      valueSelect: ""
    };
  },
  computed: {
    defaultMemberList() {
      if (this.defaultMember.length > 0) {
        for (let i = 0; i < this.defaultMember.length; i++) {
          if (this.defaultMember[i].name.length == 2) {
            this.defaultMember[i].name += "  ";
          }
        }
      }
      return this.defaultMember;
    },
    myValue() {
      return this.value !== "" ? this.value.split(",") : [];
    }
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  created() {
    this.initDefaultData()
  },
  mounted() {
    this.$nextTick(function() {
      // Code that will run only after the
      // entire view has been rendered
      let arr = [];
      if (this.value !== "") {
        arr = this.value.split(",");
        for (let i = 0; i < this.defaultMember.length; i++) {
          for (let j = 0; j < arr.length; j++) {
            if (this.defaultMember[i].id == arr[j]) {
              this.$refs.memberLi[i].className = "li-focus"; // 添加类
            }
          }
        }

        this.chooseMember = this.$refs.tree.getCheckedNodes();
        for (let k = 0; k < this.chooseMember.length; k++) {
          if (this.chooseMember[k].children !== undefined) {
            this.chooseMember.splice(k, 1);
          }
        }
      }
    });
  },
  updated: function () {
    this.$nextTick(function () {
      // Code that will run only after the
      // entire view has been re-rendered
      let arr = [];
      if (this.value !== "") {
        arr = this.value.split(",");
        for (let i = 0; i < this.defaultMember.length; i++) {
          for (let j = 0; j < arr.length; j++) {
            if (this.defaultMember[i].id == arr[j]) {
              this.$refs.memberLi[i].className = "li-focus"; // 添加类
            }
          }
        }

        this.chooseMember = this.$refs.tree.getCheckedNodes();
        for (let k = 0; k < this.chooseMember.length; k++) {
          if (this.chooseMember[k].children !== undefined) {
            this.chooseMember.splice(k, 1);
          }
        }
      }
    })
  },
  methods: {
    //不是直接更新值，而是使用此方法来对输入值进行格式化和位数限制
    updateValue: function(value) {
      // var formattedValue = value.trim()
      var formattedValue = value;
      //如果值不统一，手动覆盖以保持一致
      if (formattedValue !== value) {
        this.$refs.input.value = formattedValue;
      }
      //通过input 事件发出数值
      this.$emit("input", formattedValue);
    },
    initDefaultData(){
        const _self = this
        axios({
          //获取select-init初始化
          url: this.contextPath + "/selectUser/init",
          method: "get"
        }).then(function(response) {
          console.log("init data");
          console.log(response.data);
          const groups = response.data.data.groups

          if(groups.length >0){
              const groupMembers = response.data.data.groups[0].groupMembers
              console.log(groupMembers)
              //
              _self.options = []
              for(let i=0;i<groups.length;i++){
                let obj ={
                  groupName:'',
                  groupId:''
                }
                obj.groupName =  groups[i].groupName
                obj.groupId =  groups[i].groupId
                _self.options.push(obj)
              }
          }

        });
    },
    filterNode(value, data) {//通过关键字过滤树节点
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    memberSelect(index, id, name) {//常用人员点击
      if (this.$refs.memberLi[index].className.length <= 0) {
        //首先先判断当前li有没有被选中，因为我这里li除了选中状态的有类名，其他没有类名，所以我就这么判断了。
        //这么看有时候舍弃一小丢丢规范的东西反而省力。
        this.$refs.memberLi[index].className = "li-focus"; // 添加类
        let obj = {
          id: "",
          label: ""
        };
        obj.id = id;
        obj.label = name;
        this.chooseMember.push(obj);
        this.$refs.tree.setCheckedNodes(this.chooseMember);
      } else {
        //当前li已经被选中，那么在多选的逻辑里，是允许人们选中后再取消选中的，所以前端展示层面上把样式去掉。
        this.$refs.memberLi[index].className = ""; // 选中再取消的情况
        for (let i = 0; i < this.chooseMember.length; i++) {
          if (this.chooseMember[i].id == id) {
            this.chooseMember.splice(i, 1);
          }
        }
        this.$refs.tree.setCheckedNodes(this.chooseMember);
      }
    },
    checkChange(data, check) {
      console.log("1")
      //节点选中状态发生变化时的回调
      for (let k = 0; k < this.chooseMember.length; k++) {
        //处理常用人员被选中后，需要设置节点也是被选中，这个处理数据被添加两遍bug
        if (this.chooseMember[k].id == data.id) {
          this.chooseMember.splice(k, 1);
        }
      }
      //处理常选人员
      if (check) {
        //节点被选中状态下，同时查询常用人员是匹配，也是需要被选中
        if (data.type == 'PENSON') {
          for (let i = 0; i < this.defaultMember.length; i++) {
            if (this.defaultMember[i].id == data.id) {
              this.$refs.memberLi[i].className = "li-focus"; // 添加类
            }
          }
          //处理添加选中人员
          this.chooseMember.push(data);
        }
      } else {

        for (let l = 0; l < this.defaultMember.length; l++) {
          if (this.defaultMember[l].id == data.id) {
            this.$refs.memberLi[l].className = ""; // 添加类
          }
        }

        for (let k = 0; k < this.chooseMember.length; k++) {
          //处理选中被取消的
          if (this.chooseMember[k].id == data.id) {
            this.chooseMember.splice(k, 1);
          }
        }
      }
    },
    handleChange(val) {
      console.log(val);
    },
    defaultSetting(id) {//默认设置
      const _self = this
      axios({
        url: this.contextPath + `/selectUser/setDefaultGroup?groupId=${id}`,
        method: "get"
      }).then(function(response) {
        console.log("默认设置");
        console.log(response.data);
        _self.$message({
          message: response.data.msg,
          type: 'success'
        });
        _self.initDefaultData()
      });
    },
    deleteGroupMember(id) {//删除分组成员
      const _self = this
      axios({
        url: this.contextPath + `/selectUser/deleteGroup?groupId=${id}`,
        method: "get"
      }).then(function(response) {
        console.log("删除分组成员");
        console.log(response.data);
        _self.$message({
          message: response.data.msg,
          type: 'success'
        });
        _self.initDefaultData()
      });
    },
    tagClose(id) {//close tag标签
      for (let i = 0; i < this.chooseMember.length; i++) {
        //处理选中被取消的
        if (this.chooseMember[i].id == id) {
          this.chooseMember.splice(i, 1);
        }
      }
      this.$refs.tree.setCheckedNodes(this.chooseMember);
    },
    all_delete() {//全部删除
      this.$refs.tree.setCheckedKeys([]);
    },
    save_group_name() {//保存自定义分组名字
      const val = this.editInput;
      const _self = this
      if (val == "") {
        this.$message({
          message: "请输入内容"
        });
      } else if (val.length > 25) {
        this.$message({
          message: "请输入小于20字符"
        });
      } else {
        const id = "memberId";
        const name = "memberName";
        let chooseArr = this.chooseMember
        for (let i = 0; i < chooseArr.length; i++) {
          // 复制原来的值
          chooseArr[i][id] = chooseArr[i]["id"];
          chooseArr[i][name] = chooseArr[i]["label"];
          // 删除原来的键
          delete chooseArr[i]["id"];
          delete chooseArr[i]["label"];
        }

        let memberObjString = JSON.stringify(chooseArr);

        var params = new URLSearchParams();
        params.append("groupName", val);
        params.append("groupMembers", memberObjString);
        params.append("isDefault", 1);

        const URL = this.contextPath + '/selectUser/saveGroup'
        axios
          .post(URL, params)
          .then(function(response) {
            _self.dialogTableVisible = false
            _self.$message({
              message: response.data.msg,
              type: 'success'
            });
            _self.initDefaultData()

          })
          .catch(function(error) {
            console.log(error);
          });

      }
    },
    handleSave(){//保存
        const _self = this
        const id = "userId";
        const name = "userName";
        let chooseArr  = [] //值传递和地址传递
        chooseArr = this.chooseMember
        let idString = []

        for (let i = 0; i < chooseArr.length; i++) {
          // 复制原来的值
          chooseArr[i][id] = chooseArr[i]["id"];
          chooseArr[i][name] = chooseArr[i]["label"];
          // 删除原来的键
          delete chooseArr[i]["id"];
          delete chooseArr[i]["label"];
        }

        let memberObjString = JSON.stringify(chooseArr);
        var params = new URLSearchParams();
        params.append("selectUsers", memberObjString);

        const URL = this.contextPath + '/selectUser/saveSelectUser'
        // axios
        //   .post(URL, params)
        //   .then(function(response) {
        //     _self.$message({
        //       message: response.data.msg,
        //       type: 'success'
        //     });

        //     //_self.$refs.memberLi.className = ""; // 添加类
        //     for (let k = 0; k < chooseArr.length; k++) {
        //       idString.push(chooseArr[k].id)
        //     }
        //     const string_id = idString.join(",");
        //     //v-model
        //     _self.updateValue(string_id);

        //     //将选中的的数据清空,
        //     const id = "id";
        //     const name = "label";
        //     for (let j = 0; j < _self.chooseMember.length; j++) {
        //       // 复制原来的值
        //       _self.chooseMember[j][id] = _self.chooseMember[j]["userId"];
        //       _self.chooseMember[j][name] = _self.chooseMember[j]["userName"];
        //       // 删除原来的键
        //       delete _self.chooseMember[j]["userId"];
        //       delete _self.chooseMember[j]["userName"];
        //     }
        //     _self.all_delete()
        //   })
    },
    defaultChoose(arr,id){

    }
  }
};
</script>

<style scoped >
.transfer-contanier .inner {
  height: 384px;
  border: 1px solid #ccc;
}
.transfer-contanier .inner .tree-left {
  float: left;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 8px;
  border-right: 1px solid #ccc;
  width: 346px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  height: 100%;
}
.transfer-contanier .inner .tree-left .tree-box {
  height: 253px;
  overflow-y: auto;
}
.transfer-contanier .inner .tree-left .list-member {
  padding: 5px 0 5px 0;
}
.transfer-contanier .inner .tree-left .list-member h2 {
  color: #333;
  font-size: 14px;
  font-weight: bold;
}
.transfer-contanier .inner .tree-left .list-member ul {
  display: flex;
  flex-wrap: wrap;
}
.transfer-contanier .inner .tree-left .list-member ul li {
  margin-top: 4px;
  width: 78px;
  line-height: 24px;
  height: 24px;
  text-align: center;
  border: 1px solid #ccc;
  margin-right: 5px;
  box-sizing: border-box;
  border-radius: 4px;
  cursor: pointer;
}
.transfer-contanier .inner .tree-left .list-member ul li:nth-child(4n) {
  margin-right: 0;
}
.tree-left .list-member ul .li-focus {
  color: #b50005;
}
.tree-left .list-member ul li i {
  display: none;
}
.transfer-contanier .inner .tree-left .list-member ul .li-focus i {
  display: inline-block;
}
.transfer-contanier .inner .tree-left .button-box {
  display: flex;
}
.transfer-contanier .inner .tree-left .button-box {
  display: flex;
}
/*list-right start*/
.transfer-contanier {
  width: 789px;
  margin: 0 auto;
}
.transfer-contanier .inner .list-right {
  float: left;
  width: 56%;
  box-sizing: border-box;
  padding: 10px 10px;
  height: 100%;
  position: relative;
}
.transfer-contanier .inner .list-right .tag-box span {
  margin-top: 10px;
}
.transfer-contanier .inner .list-right .button-nav {
  position: absolute;
  bottom: 8px;
  right: 8px;
}

.transfer-contanier .inner .list-right .el-select-dropdown__item.hover,
.el-select-dropdown__item:hover .icon-nav {
  display: block !important;
}
.transfer-contanier .inner .list-right .choose-member-content {
  border: 1px solid #ccc;
  height: 242px;
  border-radius: 9px;
  padding: 0px 6px 4px;
  box-sizing: border-box;
  margin-top: 10px;
  background: rgba(204, 204, 204, 0.2);
  position: relative;
}

/*list-right end*/
.transfer-contanier .button-bottom {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
.transfer-contanier .button-bottom button {
  height: 34px;
  width: 78px;
  background-color: #b50005;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #fff;
  font-size: 12px;
  cursor: pointer;
}
.transfer-contanier .button-bottom button img {
  height: 20px;
  width: 20px;
}
/*清除float  */
.clearfix:after {
  content: "";
  display: block;
  visibility: hidden;
  clear: both;
}
</style>





